import exitImg from 'assets/images/exit.png';
import vaultImage from 'assets/images/vault_team_noletter.jpg';
// import Timer from 'components/Timer';
import config from 'config';
import { useDisconnectWallet } from 'hooks';
import useFetch from 'hooks/useFetch';
import moment from 'moment';
import { useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import {
  CartesianGrid,
  // Legend,
  Line,
  LineChart,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';
import { assetService } from 'services/blockchain/assetService';
import { store } from 'store';
import { useAppSelector } from 'store';
import { setTokenIds, setUsedTokenIdsOf } from 'store/actions/globalActions';
import { getFloorPrice } from 'utils/alchemy';

import { Box, Button, Link, Typography } from '@mui/material';
import { useWeb3React } from '@web3-react/core';

const PRICE_URL = 'https://api.coingecko.com/api/v3/coins/';
const TYPE_PERIOD = '/market_chart?vs_currency=usd&days=30/interval=1m';

const CustomFloorTooltip = ({ active, payload, label }) => {
  if (active && payload && payload.length) {
    return (
      <Box sx={{ padding: '10px' }}>
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            color: '#db9b13',
            textDecoration: 'none',
            fontSize: 16,
          }}
        >
          Time: {label}
        </Typography>
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            color: '#db9b13',
            textDecoration: 'none',
            fontSize: 16,
          }}
        >
          Floor: ${payload[0].value}
        </Typography>
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            color: '#db9b13',
            textDecoration: 'none',
            fontSize: 16,
          }}
        >
          Backing: ${payload[1].value.toFixed(2)}
        </Typography>
      </Box>
    );
  }

  return null;
};

const CustomVaultTooltip = ({ active, payload, label }) => {
  if (active && payload && payload.length) {
    return (
      <Box sx={{ padding: '10px' }}>
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            color: '#db9b13',
            textDecoration: 'none',
            fontSize: 16,
          }}
        >
          Day: {label}
        </Typography>
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            color: '#db9b13',
            textDecoration: 'none',
            fontSize: 16,
          }}
        >
          $ USD: {payload[0].value.toFixed(2)}
        </Typography>
      </Box>
    );
  }

  return null;
};

function Vault({ sx }) {
  const [prices, setPrices] = useState();
  const [floorPrices, setFloorPrices] = useState();
  const [floor, setFloor] = useState();
  const [tvcl, setTVCL] = useState();
  const [amountList, setAmountList] = useState();
  const [ethPrice, setEthPrice] = useState(0);
  // const [loading, setLoading] = useState(false);

  const coinlist = [
    'chainlink',
    'shiba-inu',
    'dogelon-mars',
    'pax-gold',
    'quant-network',
    'hex',
    'usd-coin',
    'old-bitcoin',
    'laika',
    'ethereum',
  ];

  const getPriceData = async (coin) => {
    return fetch(`${PRICE_URL}${coin}${TYPE_PERIOD}`, {
      mode: 'cors',
      // credentials: 'include',
    });
  };

  const getProcessedPriceData = async (response) => {
    const promises = response.map((res) => res.json());
    return Promise.all(promises);
  };

  const getFloorData = async () => {
    const res = await fetch(`${config.apiBaseURL}floor`, {
      mode: 'cors',
      // credentials: 'include',
    });
    const prices = await res.json();
    return prices;
  };

  const { active, account } = useWeb3React();
  const { tokenIds, claimable, usedTokenIdsOf } = useAppSelector((state) => state.global);
  const disconnectWallet = useDisconnectWallet();
  const { data } = useFetch(`${config.apiBaseURL}coins`);

  const getAll = async () => {
    // setLoading(true);
    try {
      const floorData = await getFloorData();
      const floorVals = floorData.reverse().map((data) => {
        return {
          name: moment.unix(data.timestamp).format('HH:mm'),
          floor: (data.eth * data.price).toFixed(2),
        };
      });

      console.log('1');
      const coinPromises = coinlist.map((coin) => getPriceData(coin));
      const apiRes = await Promise.all(coinPromises);
      console.log('2');

      const res = await getProcessedPriceData(apiRes);
      const lengths = res.map(({ prices }) => prices.length);
      const minLength = Math.min(...lengths);
      const newRes = res.map(({ prices }) => prices.map((val) => val[1]));

      let data = [];
      for (let i = 0; i < minLength; i++) {
        data.push({
          chainlink: newRes[0][i],
          'shiba-inu': newRes[1][i],
          'dogelon-mars': newRes[2][i],
          'pax-gold': newRes[3][i],
          'quant-network': newRes[4][i],
          hex: newRes[5][i],
          'usd-coin': newRes[6][i],
          'old-bitcoin': newRes[7][i],
          laika: newRes[8][i],
          ethereum: newRes[9][i],
        });
      }
      // get latest price and calculate the proportion for each asset
      const latest = minLength - 1;
      let totalAmount =
        data[latest]['chainlink'] * 408 +
        data[latest]['shiba-inu'] * 100902622 +
        data[latest]['dogelon-mars'] * 3445238310 +
        data[latest]['pax-gold'] * 0.55 +
        data[latest]['quant-network'] * 21.5 +
        data[latest]['hex'] * 28404 +
        data[latest]['laika'] * 17412306698 +
        data[latest]['old-bitcoin'] * 27288 +
        data[latest]['usd-coin'] * 1000;
      setTVCL(totalAmount);
      let assetAmountList = {
        chainlink: data[latest]['chainlink'] * 408,
        'shiba-inu': data[latest]['shiba-inu'] * 100902622,
        'dogelon-mars': data[latest]['dogelon-mars'] * 3445238310,
        'pax-gold': data[latest]['pax-gold'] * 0.55,
        'quant-network': data[latest]['quant-network'] * 21.5,
        hex: data[latest]['hex'] * 28404,
        laika: data[latest]['laika'] * 17412306698,
        'old-bitcoin': data[latest]['old-bitcoin'] * 27288,
        'usd-coin': data[latest]['usd-coin'] * 1000,
      };
      setAmountList(assetAmountList);
      setEthPrice(data[latest]['ethereum']);
      const newUSD = data.map((item, index) => {
        return {
          name:
            moment()
              .subtract(minLength - index, 'days')
              .date() == 1
              ? moment()
                  .subtract(minLength - index, 'days')
                  .format('D MMM')
              : moment()
                  .subtract(minLength - index, 'days')
                  .format('D'),
          Vault:
            item['chainlink'] * 408 +
            item['shiba-inu'] * 100902622 +
            item['dogelon-mars'] * 3445238310 +
            item['pax-gold'] * 0.55 +
            item['quant-network'] * 21.5 +
            item['hex'] * 28404 +
            item['laika'] * 17412306698 +
            item['old-bitcoin'] * 27288 +
            item['usd-coin'] * 1000,
          // Floor: item['ethereum'] * floor,
        };
      });
      setPrices(newUSD.slice(-20));
      const floorValues = floorVals.map(({ name, floor }, index) => {
        return {
          name,
          floor,
          backing: newUSD.slice(-floorVals.length)[index]['Vault'] / 1750,
        };
      });
      setFloorPrices(floorValues);
      // setLoading(false);
    } catch (e) {
      // setLoading(false);
      toast.error('Network request failed!');
      console.log(e);
    }
  };

  const getFloor = async () => {
    const price = await getFloorPrice();
    setFloor(price);
  };

  // useEffect(() => {
  //   if (active) {
  //     store.dispatch(setTokenIds(account));
  //     store.dispatch(setUsedTokenIdsOf(account));
  //     // config.tokens.map((token) => {
  //     //   getTokenBalance(token);
  //     // });
  //   }
  // }, [active, account]);

  // useEffect(() => {
  //   // getPriceData('chainlink');
  // }, []);

  useEffect(() => {
    getFloor();
    getAll();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onClaim = async () => {
    const remain = tokenIds.filter((id) => !usedTokenIdsOf.includes(id));
    let tx;
    if (remain.length >= 100) {
      tx = await assetService.claimAsset(account, remain.slice(0, 100));
    } else {
      tx = await assetService.claimAsset(account, remain);
    }
    if (tx !== null) {
      toast.success('Successfully claimed!');
      store.dispatch(setUsedTokenIdsOf(account));
      store.dispatch(setTokenIds(account));
    }
  };

  const handleExit = async () => {
    await disconnectWallet();
  };

  console.log('floor', floorPrices);

  return (
    <Box
      sx={{
        backgroundImage: `url(${vaultImage})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        backgroundPosition: 'center top',
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '0 0 50px 0',
        '@media (min-width: 425px)': {
          padding: '0 0 50px 0',
        },
        '@media (min-width: 768px)': {
          padding: '0 0 50px 0',
        },
        '@media (min-width: 1024px)': {
          padding: '0 0 100px 0',
        },
        '@media (min-width: 1440px)': {
          padding: '0 0 100px 0',
        },
        '@media (min-width: 1920px)': {
          padding: '0 0 100px 0',
        },
        marginBottom: '15px',
        ...sx,
      }}
    >
      {/* {loading && <Box>Loading</Box>} */}
      {active && (
        <Box
          sx={{
            position: 'absolute',
            right: '15px',
            top: '135px',
            width: '25px',
            height: '25px',
            '@media (min-width: 375px)': {
              right: '25px',
              top: '135px',
              width: '30px',
              height: '30px',
            },
            '@media (min-width: 425px)': {
              right: '25px',
              top: '120px',
              width: '30px',
              height: '30px',
            },
            '@media (min-width: 768px)': {
              right: '35px',
              top: '175px',
              width: '45px',
              height: '45px',
            },
            '@media (min-width: 1024px)': {
              right: '50px',
              top: '235px',
              width: '60px',
            },
            '@media (min-width: 1440px)': {
              right: '50px',
              top: '215px',
              width: '70px',
              height: '70px',
            },
            '@media (min-width: 1920px)': {
              right: '70px',
              top: '240px',
              width: '80px',
            },
            cursor: 'pointer',
          }}
          onClick={handleExit}
        >
          <Box
            component="img"
            src={exitImg}
            sx={{
              width: '100%',
            }}
          />
        </Box>
      )}
      <Box
        sx={{
          padding: '25px 0 40px 0',
          '@media (min-width: 375px)': {
            padding: '25px 0 30px 0',
          },
          '@media (min-width: 425px)': {
            padding: '20px 0',
          },
          '@media (min-width: 768px)': {
            padding: '30px 0',
          },
          '@media (min-width: 1024px)': {
            padding: '40px 0',
          },
          '@media (min-width: 1440px)': {
            padding: '15px 0',
          },
          '@media (min-width: 1920px)': {
            padding: '30px 0',
          },
        }}
      >
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            color: '#db9b13',
            textDecoration: 'none',
            fontSize: 30,
            '@media (min-width: 425px)': {
              fontSize: 40,
            },
            '@media (min-width: 768px)': {
              fontSize: 60,
              letterSpacing: 5,
            },
            '@media (min-width: 1024px)': {
              fontSize: 70,
              letterSpacing: 10,
            },
            '@media (min-width: 1440px)': {
              fontSize: 90,
              letterSpacing: 15,
            },
            '@media (min-width: 1920px)': {
              fontSize: 110,
              letterSpacing: 20,
            },
          }}
        >
          Analytics Dashboard
        </Typography>
      </Box>
      <Box sx={{ justifyContent: 'flex-start', width: '80%' }}>
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            display: 'block',
            color: 'black',
            textDecoration: 'none',
            fontSize: 22,
            '@media (min-width: 768px)': {
              WebkitTextStroke: '2px #d89810',
              fontSize: 32,
            },
            '@media (min-width: 1024px)': {
              fontSize: 42,
            },
            WebkitTextStroke: '1px #d89810',
          }}
        >
          Total Claimable Value Locked(TCVL): {`$${tvcl ? tvcl.toFixed(2) : ''}`}
          <br />
          Vault Backing Per NFT:{' '}
          {`$${tvcl ? (tvcl / config.totalSupply).toFixed(2) : ''} / ${
            ethPrice > 0 ? (tvcl / config.totalSupply / ethPrice).toFixed(3) : ''
          } ETH`}
        </Typography>
      </Box>
      {!active && (
        <Box
          sx={{
            display: 'grid',
            rowGap: 2,
            columnGap: 4,
            width: '80%',
            '@media (min-width: 1025px)': {
              gridTemplateColumns: 'repeat(1, 1fr)',
            },
            justifyContent: 'flex-start',
          }}
        >
          {config['assets'] &&
            config['assets'].map((asset, index) => (
              <Link
                key={`Vaults-Link${index}`}
                target="_blank"
                href={config['coinGeckoBaseUrl'] + asset['url']}
                sx={{
                  fontFamily: 'LapsusBold',
                  display: 'block',
                  color: 'black',
                  textDecoration: 'none',
                  fontSize: 20,
                  '@media (min-width: 768px)': {
                    WebkitTextStroke: '2px #d89810',
                    fontSize: 30,
                  },
                  '@media (min-width: 1024px)': {
                    fontSize: 40,
                  },
                  WebkitTextStroke: '1px #d89810',
                }}
              >
                {`${asset['name']} (${asset['symbol']}) / ${
                  amountList
                    ? `$${amountList[asset['url']] ? amountList[asset['url']].toFixed(2) : ''} / ${
                        amountList[asset['url']]
                          ? ((amountList[asset['url']] / tvcl) * 100.0).toFixed(2)
                          : ''
                      }%`
                    : ''
                }`}
              </Link>
            ))}
        </Box>
      )}
      {active && (
        <Box
          sx={{
            marginTop: '20px',
            width: '90%',
            '@media (min-width: 1024px)': {
              marginTop: '50px',
            },
          }}
          flex
          flexBasis="column"
          alignItems="center"
        >
          <Box
            sx={{
              maxWidth: '700px',
              margin: 'auto',
            }}
          >
            {data &&
              data.map((coin, index) => {
                if (index === 2) {
                  return (
                    <Typography
                      key={`Asset-${index}`}
                      sx={{
                        fontFamily: 'LapsusBold',
                        fontSize: 23,
                        '@media (min-width: 425px)': {
                          fontSize: 25,
                        },
                        '@media (min-width: 768px)': {
                          WebkitTextStroke: '2px #d89810',
                          fontSize: 40,
                        },
                        '@media (min-width: 1024px)': {
                          fontSize: 50,
                        },
                        textAlign: 'left',
                        WebkitTextStroke: '1px #d89810',
                      }}
                    >
                      {`${coin.name}: ${Number(
                        ((tokenIds.length - usedTokenIdsOf.length) * coin.amount) /
                          config.totalSupply,
                      ).toFixed(0)} ${coin.symbol}`}
                    </Typography>
                  );
                } else {
                  return (
                    <Typography
                      key={`Asset-${index}`}
                      sx={{
                        fontFamily: 'LapsusBold',
                        fontSize: 23,
                        '@media (min-width: 425px)': {
                          fontSize: 25,
                        },
                        '@media (min-width: 768px)': {
                          WebkitTextStroke: '2px #d89810',
                          fontSize: 40,
                        },
                        '@media (min-width: 1024px)': {
                          fontSize: 50,
                        },
                        textAlign: 'left',
                        WebkitTextStroke: '1px #d89810',
                      }}
                    >
                      {`${coin.name}: ${Number(
                        ((tokenIds.length - usedTokenIdsOf.length) * coin.amount) /
                          config.totalSupply,
                      ).toFixed(4)} ${coin.symbol}`}
                    </Typography>
                  );
                }
              })}
          </Box>
          <Box textAlign="center">
            <Button
              sx={{
                marginTop: '30px',
                cursor: 'pointer',
                width: '70%',
                fontFamily: 'LapsusBold',
                fontSize: '20px',
                padding: '15px 10px',
                '@media (min-width: 768px)': {
                  marginTop: '70px',
                  fontSize: 40,
                },
                '@media (min-width: 1024px)': {
                  fontSize: 50,
                },
                color: '#fac718',
                backgroundColor: 'black',
                border: '10px solid white',
                borderRadius: '60px',
                maxWidth: '900px',
                lineHeight: '1',
                letterSpacing: '7px',
                transform: 'rotate(-5deg)',
                opacity: claimable && tokenIds.length - usedTokenIdsOf.length !== 0 ? 1 : 0.3,
                '&:disabled': {
                  color: '#fac718',
                },
                '&:hover': {
                  backgroundColor: 'rgba(0, 0, 0, 0.7)',
                },
              }}
              onClick={onClaim}
              disabled={!claimable || tokenIds.length - usedTokenIdsOf.length === 0}
            >
              CLAIM YOUR ASSETS NOW
            </Button>
          </Box>
        </Box>
      )}
      <Box sx={{ justifyContent: 'flex-start', width: '80%' }}>
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            display: 'block',
            color: 'black',
            textDecoration: 'none',
            fontSize: 22,
            '@media (min-width: 768px)': {
              WebkitTextStroke: '2px #d89810',
              fontSize: 32,
            },
            '@media (min-width: 1024px)': {
              fontSize: 42,
            },
            WebkitTextStroke: '1px #d89810',
          }}
        >
          Sugar Heads Vault Portfolio
        </Typography>
      </Box>
      <ResponsiveContainer width={'80%'} height={300} min-width={300}>
        <LineChart
          data={prices}
          margin={{
            top: 20,
            right: 30,
            left: 20,
            bottom: 15,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" stroke="#eee" />
          <XAxis
            dataKey="name"
            stroke="#dc982c"
            label={{
              value: 'Time',
              position: 'center',
              fill: '#dc982c',
              transform: 'translate(0, 15)',
            }}
            // dy={10}
            // tick={false}
          />
          <YAxis
            stroke="#dc982c"
            label={{
              value: '$ USD',
              angle: -90,
              position: 'insideLeft',
              fill: '#dc982c',
            }}
            dx={2}
            // tick={false}
          />
          <Tooltip content={<CustomVaultTooltip />} />
          <Line type="monotone" dataKey="Vault" stroke="#dc982c" activeDot={{ r: 8 }} dot={false} />
        </LineChart>
      </ResponsiveContainer>
      <Box sx={{ justifyContent: 'flex-start', width: '80%' }}>
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            display: 'block',
            color: 'black',
            textDecoration: 'none',
            fontSize: 22,
            '@media (min-width: 768px)': {
              WebkitTextStroke: '2px #d89810',
              fontSize: 32,
            },
            '@media (min-width: 1024px)': {
              fontSize: 42,
            },
            WebkitTextStroke: '1px #d89810',
          }}
        >
          NFT Analytics
          <br />
          Floor Price :
          {` $ ${ethPrice > 0 ? (floor * ethPrice).toFixed(3) : ''} / ${
            floor ? floor.toFixed(2) : ''
          } ETH`}
        </Typography>
      </Box>
      <ResponsiveContainer width={'80%'} height={300} min-width={300}>
        <LineChart
          data={floorPrices}
          margin={{
            top: 20,
            right: 30,
            left: 20,
            bottom: 15,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" stroke="#eee" />
          <XAxis
            dataKey="name"
            stroke="#dc982c"
            label={{
              value: 'Time',
              position: 'center',
              fill: '#dc982c',
              transform: 'translate(0, 15)',
            }}
            padding={{ right: 0, bottom: 100 }}
            // tick={{ transform: 'translate(0, 3)' }}
            // dy={10}
            // tick={false}
          />
          <YAxis
            stroke="#dc982c"
            label={{
              value: '$ USD',
              angle: -90,
              position: 'insideLeft',
              fill: '#dc982c',
            }}
            dx={2}
            // tick={false}
          />
          <Tooltip content={<CustomFloorTooltip />} />
          {/* <Legend /> */}
          <Line
            type="monotone"
            dataKey="floor"
            stroke="#dc982c"
            // activeDot={{ r: 8 }}
            dot={false}
          />
          <Line
            type="monotone"
            dataKey="backing"
            stroke="#dc982c"
            // activeDot={{ r: 8 }}
            dot={false}
          />
        </LineChart>
      </ResponsiveContainer>
      <Box sx={{ justifyContent: 'flex-start', width: '80%' }}>
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            display: 'block',
            color: 'black',
            textDecoration: 'none',
            fontSize: 22,
            '@media (min-width: 768px)': {
              WebkitTextStroke: '2px #d89810',
              fontSize: 32,
            },
            '@media (min-width: 1024px)': {
              fontSize: 42,
            },
            WebkitTextStroke: '1px #d89810',
          }}
        >
          #NFTs You Hold : {tokenIds.length - usedTokenIdsOf.length}
          <br />
          Your Claimable Value After Unlock :
          {` $ ${
            tvcl > 0 ? ((tvcl / 1750) * (tokenIds.length - usedTokenIdsOf.length)).toFixed(2) : ''
          } `}
        </Typography>
      </Box>
    </Box>
  );
}

export default Vault;
